/**
My width values are different from those on the figma file. 
This is to accomodate paddings and borders, where applicable
*/

.form-header {
    text-align: left;
    margin-bottom: 2em;
    display: block;
}
.form-header h1 {
    margin: 0;
}
.Contact {
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    padding: 64px 0 96px 0;
}
form {
    width: 720px;
}
form > * {
    margin-bottom: 1em;
    width: 100%;
}
.names {
    display: flex;
    gap: 8px;
    margin-bottom: 1em;
}
.names > * {
    width: 100%;
}
label {
    font-weight: 500;
}
input, textarea {
    border: 1px solid #D0D5DD;
    margin-top: 0.5em;
}
#first_name, #last_name {
    /*min-width: 320px;*/
    padding: 10px 14px;
    border-radius: 8px;
    display: block;
}
#email, #message {
    width: 692px;
    padding: 10px 14px;
    border-radius: 8px;
}
#email {
    margin-bottom: 1em;
}
#message {
    height: 132px;
    margin-bottom: 1em;
}
.checkbox {
    display: flex;
    gap: 0.8em;
    margin-top: 1em;
}
.checkbox label {
    font-weight: 300;
}
#btn__submit {
    width: 720px;
    padding: 10px 14px;
    border-radius: 8px;
    /*margin-top: 1.5em;*/
    background-color: #1570EF;
    color: #fff;
    border: 1px solid #1570EF;
    font-weight: 600;
    transition: color 0.2s ease;
    transition: background-color 0.2s ease;
}
#btn__submit:hover {
    background-color: #3983e4;
    color: #EEE;
    cursor: pointer;
}


@media screen and (min-width: 768px) {
    #first_name, #last_name {
        min-width: 320px;
    }
}
@media screen and (max-width: 768px) {
    .Contact {
        max-width: 92%;
        margin-left: auto;
        margin-right: auto;
        padding: 64px 0 64px 0;
    }
    .names {
        flex-direction: column;
    }
    .form-header, form, #first_name, #last_name, #email, #message, #btn__submit {
        width: 88%;
    }
}