.footer { 
    display:flex;
    gap:1em;
    justify-content: space-between;
    margin: 3em 0 2em;
    margin-left:auto;
    margin-right:auto;
    max-width: 90%;
    padding-top: 1.5em;
    border-top: 1px solid #D0D5DD;
}
.footer img{ 
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
}
#footerText{
    margin-top:.5em;
}
@media screen and (max-width: 768px) {
    footer {
        border-top: 1px solid #D0D5DD;
        max-width: 90%;
        margin-left: auto;
        margin-right: auto;
    }
    .footer{ 
        flex-direction: column;
        gap: .1em;
        margin-left: 1em;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        border: none;
    }
    .footer img{
        margin-bottom:.7em;
        max-width:150px;
    }
    #i4g{
        margin-top:.4em;
        max-width:200px;
    }
    #footerText{
        margin:0;
    }
}