@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
  font-family: 'Inter', serif;
}
body{
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale;
  font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
  margin:0;
  padding:0;
}
code{ 
  font-family:source-code-pro,Menlo,Monaco,Consolas,Courier New,monospace;
}
.App{ 
  text-align:center;
}
.App-header{ 
  align-items:center;
  /*display:flex;
  flex-direction:column;*/
  font-size:calc(10px + 2vmin);
  margin-bottom:3em;
  margin-top:2em;
}
.share img {
  width: 45px;
  height: 45px;
  position: absolute;
  float: right;
  top: 2em;
  bottom: 0;
  right: 0;
  left: 80%;
  cursor: pointer;
}
#shareDots {
  display: none;
}
a {
  text-decoration: none;
  color: #000;
}
header img{ 
  border-radius:50%;
  margin:0;
  width: 5em;
}
.profile {
  cursor: pointer;
}
.myLinks {
  margin-left:auto;
  margin-right:auto;
  max-width: 90%;
}
.myLinks a {
  display: block;
  color:#444;
  margin-bottom: 1.3em;
  background-color:#EAECF0;
  font-weight:600;
  border: none;
  border-radius: 8px;
  text-decoration:none;
  transition: color .25s linear;
  transition: background-color .25s ease-in;
  padding: 1em;
}
.myLinks a:hover { 
  color:#222;
  text-decoration:none;
  background-color: #ddd;
}
#twitter{ 
  margin-top:.5em;
}
#twitter a{ 
  color:#333;
  font-weight:600;
  transition:color .35s ease-in;
}
#twitter a:hover{ 
  color:#666;
  text-decoration:none;
}
#slack{
  display:none
}
.social-links{ 
  display:flex;
  gap:.75em;
  justify-content:center;
  margin-top:2.5em;
  padding-bottom: 1.5em;
}
.social-links img{ 
  max-width: 24px;
}
/**/
@media screen and (max-width:768px) {
  .share img {
    float: right;
    top: 2em;
    left: 75%;
  }
  #share {
    display: none;
  }
  #shareDots {
    display: unset;
  }
  .myLinks{ 
    max-width: 85%;
  }
  /**/
}
